.App {
  text-align: center;
  overflow: hidden;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.content {
  min-width: 275px;
}

.support {
  color: #bfc0c2;
}

.countdown {
  display: flex;
  justify-content: space-between;
}

.countdown > *:not(:last-of-type) > .countdown__number:after {
  content: ':';
  position: absolute;;
}

.countdown__number {
  font-family: 'Roboto Mono', monospace;
  font-size: calc(24px + 2vmin);
  margin: 0;
}

.countdown__title {
  margin: 0;
}
